nav {
  background-color: #000000;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 10px;
  padding-bottom: 10px;
}
nav .logo {
  width: 200px;
  color: #ffffff;
  text-transform: uppercase;
  font-weight: bold;
}
nav .logo a {
  color: #ffffff;
  text-decoration: none;
  font-family: Titles;
  letter-spacing: 1px;
}
nav .logo a:hover {
  color: #00dbde;
  text-shadow: 0 0 1px #00dbde;
}
nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
nav ul li {
  padding-left: 15px;
  padding-right: 15px;
  display: inline-block;
}
nav ul li a {
  text-decoration: none;
  color: #ffffff;
  font-weight: bold;
  font-family: SubTitles;
  letter-spacing: 1px;
}
nav ul li a:hover {
  color: #00dbde;
  text-shadow: 0 0 1px #00dbde;
}/*# sourceMappingURL=Nav.css.map */