#Games{
    background-color: #000000;
    min-height: 100vh;

    h2{
        font-size: 10vw;
        font-family: Handwriting;
        color: rgba(255,255,255,0.1);
    }

    .gamesText{
        position:relative;
        font-family: "Text";
        top: -15vh;
        left: 25vh;
        width: 100vh;
        font-size: 4vh;
        text-align: justify;
        color: #ffffff;
    }

    .item{
        h3{
            font-family: SubTitles;
            font-size: 2vw;
            text-align: center;
            color: #00dbde;
        }

        p{
            text-align: center;
            font-family: "Text";
        }
    }
}