#Web{
    background-color: #ffffff;
    min-height: 100vh;

    h2{
        color: #e7e7e7;
        font-size: 8vw;
        font-family: Handwriting;
    }

    .webText{
        position:relative;
        font-family: "Text";
        top: -12vh;
        left: 25vh;
        width: 100vh;
        font-size: 4vh;
        text-align: justify;
    }

    .item{
        h3{
            font-family: SubTitles;
            font-size: 2vw;
            text-align: center;
        }

        p{
            text-align: center;
            font-family: "Text";
        }
    }
}