nav{
    background-color: #000000;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 10px;
    padding-bottom: 10px;

    .logo{
        width: 200px;
        color: #ffffff;
        text-transform: uppercase;
        font-weight: bold;
        
        a{
            color: #ffffff;
            text-decoration: none;
            font-family: Titles;
            letter-spacing: 1px;

            &:hover{
                color: #00dbde;
                text-shadow: 0 0 1px #00dbde;
            }
        }
    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
        li{
            padding-left: 15px;
            padding-right: 15px;
            display: inline-block;
            a{
                text-decoration: none;
                color: #ffffff;
                font-weight: bold;
                font-family: SubTitles;
                letter-spacing: 1px;

                &:hover{
                    color: #00dbde;
                    text-shadow: 0 0 1px #00dbde;
                }
            }
        }
    }
}