@font-face {
  font-family: "Handwriting";
  src: url(./Fonts/Dancing/DancingScript-Bold.ttf);
}
@font-face {
  font-family: "Titles";
  src: url(./Fonts/Poppins/Poppins-Bold.ttf);
}
@font-face {
  font-family: "SubTitles";
  src: url(./Fonts/Poppins/Poppins-Medium.ttf);
}
@font-face {
  font-family: "Text";
  src: url(./Fonts/Poppins/Poppins-ExtraLight.ttf);
}
html {
  background-color: #ffffff;
}
html body h1 {
  color: #ffffff;
}
html body h2 {
  color: #ffffff;
}
html body header {
  background-color: #ffffff;
  height: 100vh;
}
html body header .content h1 {
  font-family: Handwriting;
  font-size: 6vw;
  line-height: 0.7;
  font-weight: bold;
  text-align: center;
  letter-spacing: 6px;
  color: #000000;
  /* -webkit-text-stroke: 1px #000000; */
}
html body header .content h1 small {
  font-family: Titles;
  width: 100%;
  font-size: 0.3em;
  text-shadow: none;
  color: #00dbde;
